    tab5Content: '<p><strong>Ophthalmologists Meeting 2019 Scope and Market Research:</strong></p><p>According to a study it is estimated that around 48% of world population is blind because of cataract and more than 60 million people suffer from glaucoma and it is estimated that the number would rise to 80 million by the end of 2020. Ageing population is the leading cause for refractive error problem in countries like the USA and Europe, it is noticed that the refractive error problem is mostly seen in population ageing more than 40 years.</p><p>After a huge downfall of the market during economic drain in 2008-2009, ophthalmology market is catching up to get back to the normal as there is a rapid growth in population resulting in increased number of patients with eye diseases and the incidence of cataract and glaucoma are increasing every year.</p><p><strong>Importance and scope:</strong></p><p>The field of Ophthalmology: Ocular treatment &amp; care is growing rapidly and its development is making tremendous impacts in medical and health sciences and pharmaceuticals. The importance and significance can be gauged by the fact that it has made huge advancements over the course of time and is continuing to influence various sectors. Ophthalmologists Meeting 2019 conference will concentrate on latest and exciting innovations in areas of eye surgeries, diagnostics and recent technologies towards eye care. It will also provide an excellent opportunity to meet the leading eye surgeons, top class eye care specialists, ophthalmologists, optometrists, research scientists and the young student research community working the related field.</p><p>An Ophthalmologist Meeting 2019 Conference is designed to comprise of Keynote sessions, Workshops and focused symposium on latest developments in the field of one of the sense organ Eye health. The two-day Ophthalmologist Meeting 2019 conference includes major sessions on detailed overview on Retinal diseases and disorders, Research Trends in Surgical and Medical Ophthalmology, Novel Approaches to Ophthalmology Therapeutics, Ocular-Pathology, Optometry and vision science. Ophthalmologist Meeting 2019 also acts as a platform to exchange information, new interventions and strengthen the collaboration among Ophthalmologists, Optometrists, Researchers, Associate Professors, and Scholars from both academia and ophthalmic Institutes.</p><p><strong>According to WHO:</strong></p><ul><li>285 million people are estimated to be visually impaired worldwide: 39 million are blind and 246 have low vision.</li><li>About 90% of the world&rsquo;s visually impaired live in low-income settings.</li><li>82% of people living with blindness are aged 50 and above.</li><li>Globally, uncorrected refractive errors are the main cause of moderate and severe visual impairment; cataracts remain the leading cause of blindness in middle- and low-income countries.</li><li>The number of people visually impaired from infectious diseases has reduced in the last 20 years according to global estimates work.</li><li>80% of all visual impairment can be prevented or cured.</li><li>Cataract removal is the most commonly performed ophthalmic surgical operation.</li></ul><p>&nbsp;</p><h3><br /><br /></h3>'
